import React from "react";
import { Link } from "gatsby";

export const dateFormat = (date) => {
	let input;
	if (date.indexOf(' ') > 0) {
		input = date.split(' ').join('T');
	} else if (date.indexOf('T') > 0) {
		input = date;
	}
	input = new Date(input);
	const options = {
		// weekday: 'long',
		year: 'numeric',
		month: 'long',
		day: 'numeric',
	}

	return input.toLocaleDateString('en-GB', options);
}

const Excerpt = ( {data} ) => (
	<article className="Excerpt mb-L">
		<h2 className="mb-0">
			<Link to={`/${data.fields.slug}`}>
				{data.frontmatter.title}
			</Link>
		</h2>
		<time className="block mb-M">{ dateFormat(`${data.frontmatter.date}`) }</time>
		<div dangerouslySetInnerHTML={{ __html: data.frontmatter.description || data.excerpt }} />
	</article>
)

export default Excerpt

